.publish{
  .step{
    width: 100%;
    .step1{
      width: 260px;
      height: 40px;
      float: left;
      background: url("../images/step1.png") no-repeat;
    }
    .step2{
      width: 260px;
      height: 40px;
      float: left;
      margin-left: -20px;
      background: url("../images/step2.png") no-repeat;
    }
    .step3{
      width: 260px;
      height: 40px;
      float: left;
      margin-left: -20px;
      background: url("../images/step3.png") no-repeat;
    }
    .step4{
      width: 260px;
      height: 40px;
      float: left;
      margin-left: -20px;
      background: url("../images/step4.png") no-repeat;
    }
    .text{
      width: 150px;
      height:20px;
      margin: 10px 0 0 40px;
      label{
        width: 20px;
        height:20px;
        border-radius: 50%;
        background-color: #fff;
        line-height: 20px;
        text-align: center;
      }
      span{
        color: #fff;
      }
    }
  }
  .step-title{
    line-height: 60px;
    font-size: 16px;
  }
  .form-group{
    label{
      font-weight:normal;
      text-align: right;
      line-height: 30px;
    }
    .unit{
      width: 90%;
      display: inline-block;
    }
    .range{
      width: 80%;
      display: inline-block;
    }
    .speci{
      width: 50%;
      display: inline-block;
    }
    .integral{
      width: 20%;
      display: inline-block;
    }
  }
  .tip{
    background-color: #faecd1;
    text-align: center;
    height:35px;
    line-height: 35px;
  }
}